<template>
  <section class="position-relative">
    <video
      ref="videoRef"
      controls
      autoplay
      class="w100 h100"
      controlsList="nodownload nofullscreen noremoteplayback"
      disablePictureInPicture
    />
    <div
      v-if="item.level < 5"
      class="warn-message position-absolute w100 flex-row align-center"
      :style="{ backgroundColor: bkColors[item.level - 1] + '70' }"
    >
      <span>{{ item.level }}</span>
      <span>{{ item.reason }}</span>
      <span>{{ item.value }}</span>
    </div>
    <div
      class="work-place flex-row align-center position-absolute"
      :style="{ color: bkColors[item.level - 1] || '#64E1F2' }"
    >
      <span class="mr10">{{ item.coal }} </span>
      <span>{{ item.place }} </span>
    </div>
  </section>
</template>

<script lang="js">
import { defineComponent } from "vue";
import kurentoUtils from "kurento-utils";

export default defineComponent({
  name: "video-test",
  props: {
    address: {
      type: String,
      default:
        "rtsp://wowzaec2demo.streamlock.net/vod/mp4:BigBuckBunny_115k.mov"
    },
    auto: {
      type: Boolean,
      default: false
    },
    item: {
      type: Object,
      default: () => ({
        coal: "",
        id: 4,
        level: 3,
        place: "D24386",
        reason: ""
      })
    }
  },
  data() {
    return {
      videoServer: "ws://192.168.1.206:8888/kurento",
      webRtcPeer: null,
      pipeline: null,
      bkColors: ["#FF2516", "#FAAB1B", "#FFFB00", "#06F398"]
    };
  },
  async mounted() {
    await this.$nextTick();
    if (this.auto) {
      this.start();
    }
  },
  methods: {
    start() {
      this.webRtcPeer = kurentoUtils.WebRtcPeer.WebRtcPeerRecvonly(
        { remoteVideo: this.$refs.videoRef },
        error => {
          if (error) {
            return console.error(error);
          }
          this.webRtcPeer.generateOffer(this.onOffer);
        }
      );
    },

    onError(error) {
      if (error) {
        this.end();
      }
    },
    setIceCandidateCallbacks(webRtcEndpoint, webRtcPeer, onError) {
      webRtcPeer.on("icecandidate", candidate => {
        candidate = window.kurentoClient.register.complexTypes.IceCandidate(
          candidate
        );
        webRtcEndpoint.addIceCandidate(candidate, onError);
      });
      webRtcEndpoint.on("OnIceCandidate", event => {
        const candidate = event.candidate;
        webRtcPeer.addIceCandidate(candidate, onError);
      });
    },
    onOffer(error, sdpOffer) {
      if (error) return this.onError(error);

      window.kurentoClient(this.videoServer, {}, (error, kurentoClient) => {
        if (error) return this.onError(error);
        kurentoClient.create("MediaPipeline", (error, p) => {
          if (error) return this.onError(error);
          this.pipeline = p;
          this.pipeline.create(
            "PlayerEndpoint",
            { uri: this.address },
            (error, player) => {
              if (error) return this.onError(error);

              this.pipeline.create(
                "WebRtcEndpoint",
                (error, webRtcEndpoint) => {
                  if (error) return this.onError(error);

                  this.setIceCandidateCallbacks(
                    webRtcEndpoint,
                    this.webRtcPeer,
                    this.onError
                  );

                  webRtcEndpoint.processOffer(sdpOffer, (error, sdpAnswer) => {
                    if (error) return this.onError(error);
                    webRtcEndpoint.gatherCandidates(this.onError);
                    this.webRtcPeer.processAnswer(sdpAnswer);
                  });

                  player.connect(webRtcEndpoint, error => {
                    if (error) return this.onError(error);

                    player.play(error => {
                      if (error) return this.onError(error);
                    });
                  });
                }
              );
            }
          );
        });
      });
    },
    end() {
      if (this.webRtcPeer) {
        this.webRtcPeer.dispose();
        this.webRtcPeer = null;
      }
      if (this.pipeline) {
        this.pipeline.release();
        this.pipeline = null;
      }
    }
  }
});
</script>

<style scoped>
.warn-message {
  top: 0;
  left: 0;
  height: 30px;
  padding-left: 5%;
  font-size: 1.8rem;
  font-weight: 400;
  letter-spacing: 1px;
}

.work-place {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 30px;
  padding-left: 5%;
}
</style>
